.user_tasks{
    height: 85vh;
    overflow: scroll;
    scrollbar-width: none; /* Hide scrollbars in Firefox */
}
.user_tasks::-webkit-scrollbar{
    display: none; /* Hide scrollbars in WebKit browsers */
}
.sinngle_task{
    display: flex;
    padding: 5px;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.678);
    margin-top: 5px;
}
.task_left_details{
    display: flex;
}
.task_image{
    height: 30px;
    margin-right: 10px;
}
.task_button{
    padding: 7px;
    margin: 5px;
}
.check_task_icon{
    height: 25px;
    border: 1px solid green;
    border-radius: 50%;
    margin-right: 5px;
}
.task_img_twitter{
    border: 1px solid white;
    background-color: white;
    border-radius: 8px;
}
.task_detail p{
    color:green;
    font-weight: bold;
}
.task_detail h5{
    color:black;
}