.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.8);
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    text-align: center;
    animation: popupAnimation 0.4s ease-out;
    overflow: hidden;
  }
  .popup-hide {
    opacity: 0;
    pointer-events: none; /* Disable interactions when hiding */
    animation: popupAnimationOut 0.4s ease-out;
  }
  .popup-content {
    position: relative;
  }
  
  .popup-header {
    font-size: 24px;
    margin-bottom: 10px;
    color: white;
  }
  
  .popup-day {
    font-size: 18px;
    margin: 10px 0;
    color: white;
    font-weight: bold;
  }
  
  .popup-reward {
    font-size: 22px;
    font-weight: bold;
    color: green;
  }

  .popup-reward span{
    color: white;
  }
  
  /* Emoji container */
  .emoji-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
  
  .emoji-fly {
    position: absolute;
    font-size: 24px;
    animation: fly 2s ease-in-out forwards, fadeOut 1.5s ease-in forwards;
    opacity: 1;
  }
  
  /* Faster and farther flight, no rotation */
  @keyframes fly {
    from {
      transform: translate(0, 0);
    }
    to {
      transform: translate(calc(300vw * var(--x)), calc(300vh * var(--y)));
    }
  }
  
  /* Faster fade out */
  @keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  
  @keyframes popupAnimation {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes popupAnimationOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }