.welcome_user{
    text-align: center;
}
.welcome_user img{
    width: 80px;
    border-radius: 50%;
}
.points_user{
    text-align: center;
    position: absolute;
    bottom: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color:rgb(255, 255, 255, 0.5);
    padding-top: 10px;
}
.points_user img{
    width: 60px;
    border-radius: 50%;
    border: 1px solid black;
}
.user_points_number{
    font-size: 22px;
    font-weight: bold;
    color:rgb(0, 0, 255);
}
.coin_name{
    color: black;
    font-weight: bold;
}
.cooming_soon{
    text-align: center;
}
.cooming_soon b{
    color: green;
}
.daily_rewards_container{
    width: 100%;
    position: absolute;
    bottom: 80px;
}
.daily_rewards_container h3{
    text-align: center;
    padding: 7px;
    font-size: 16px;
}
.daily_rewards{
    display: flex;
    justify-content: center;
}
.daily_rewards div{
    padding: 5px;
    text-align: center;
    border: 1px solid white;
    width: 50px;
    font-size: 13px;
}
.daily_rewards b{
    color: green;
}
.daily_rewards p{
    padding: 7px;
}
.current_daily{
    background-color: rgba(0, 255, 255, 0.192);
}
