.leaderboard{

}
.leaderboard h3{
    text-align: center;
    padding: 20px 0px;
}
.leaderboard ul{
    /* height: 64.5vh; */
    height: 64vh;
    overflow: scroll;
    scrollbar-width: none; /* Hide scrollbars in Firefox */
}
.leaderboard ul::-webkit-scrollbar{
    display: none; /* Hide scrollbars in WebKit browsers */
}
.leaderboard li{
    display: flex;
    justify-content: space-between;
    padding: 3px 10px;
}
.user_top{
    color:rgb(51, 51, 255);
}
.invite_fr_button{
    width:75%;
}
.copy_invite{
    width: 15%;
    padding: 5px;
}
.copy_invite img{
    height: 30px;
}
.friennds_buttons{
    display: flex;
}